import { useState } from "react";
import "./Accordion.scss";

const mockItems = [
  {
    id: 1,
    title: "       ?",
    body: "   ,     .          ,        .",
  },
  {
    id: 2,
    title: "  ?",
    body: ",   `,    .     ,      !",
  },
  {
    id: 3,
    title: "    ?",
    body: "200 $ -     ,         50 $.",
  },
  {
    id: 4,
    title: "  ?",
    body: "      ,   + ,    ,     .  ,  +  /.        .",
  },
];

const mockItemsV2 = [
  {
    id: 5,
    title: "  ?",
    body: " ,     4000-6000 $,    .    ,    .",
  },
  {
    id: 6,
    title: "  ?",
    body: "     ,     Facebook/Google",
  },
  {
    id: 7,
    title: "   ?",
    body: "          +  , ,     .              .",
  },
  {
    id: 8,
    title: " ?",
    body: "         . :      :     .",
  },
];

const AccordionItem = ({ id, title, body }) => {
  const [state, setState] = useState(false);

  const toggleAccordion = () => {
    setState((prev) => !prev);
  };

  return (
    <div className="accordion-item">
      <div onClick={toggleAccordion} className="accordion-trigger">
        <h4>{title}</h4>
        <div className={`accordion-icon ${state ? "is-active" : ""}`}></div>
      </div>
      <div className={`accordion-content ${state ? "is-active" : ""}`}>
        <p>{body}</p>
      </div>
    </div>
  );
};

const Accordion = () => {
  return (
    <div className="accordion">
      <div className="left">
        {mockItems.map(({ id, title, body }) => (
          <AccordionItem key={id} id={id} title={title} body={body} />
        ))}
      </div>
      <div className="right">
        {mockItemsV2.map(({ id, title, body }) => (
          <AccordionItem key={id} id={id} title={title} body={body} />
        ))}
      </div>
    </div>
  );
};

export default Accordion;
